/*
* Footer Title
*/

.footer-title {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
}


.footer-text {
    margin-top: 10vw;
    margin-bottom: 10vw;
    margin-left: 10vw;
    margin-right: 10vw;
    color: #434343;
    font-family: "Raleway", sans-serif;
    font-weight: 300;
    line-height: 30px;
}


.copyrights {
  margin-top: 8vw;
  margin-bottom: 1vw;
  margin-left: -60px;
}

/*
* Resume
*/

.download-resume {
  margin-top: 5vw;
  height: 4vw;
  width: 20vw;
}

.icoResume {
  padding-left: 8px;
}


/*
* Social Icons
*/

.social-links {
  padding-top: 3vw;
}

/* footer social icons */
ul.social-network {
    list-style: none;
	display: inline;
	margin-left:0 !important;
	padding: 0;
}
ul.social-network li {
	display: inline;
	margin: 0 5px;
}

.social-network a.icoGithub:hover {
	background-color:#000000;
}

.social-network a.icoLinkedin:hover {
	background-color:#007bb7;
}

.social-network a.icoEmail:hover {
	background-color:#ff6600;
}

 .social-network a.icoGithub:hover i, .social-network a.icoLinkedin:hover i, .social-network a.icoEmail:hover i {
	color:#fff;
}
a.socialIcon:hover, .socialHoverClass {
	color:#44BCDD;
}

.social-circle li a {
	display:inline-block;
	position:relative;
	margin:0 auto 0 auto;
	border-radius:50%;
	text-align:center;
	width: 50px;
	height: 50px;
	font-size:20px;
}
.social-circle li i {
	margin:0;
	line-height:50px;
	text-align: center;
}

.social-circle li a:hover i, .triggeredHover {
	-moz-transform: rotate(360deg);
	-webkit-transform: rotate(360deg);
	-ms--transform: rotate(360deg);
	-ms-transform: rotate(360deg);
	    transform: rotate(360deg);
	-webkit-transition: all 0.2s;
	-o-transition: all 0.2s;
	transition: all 0.2s;
}
.social-circle i {
	color: #fff;
	-webkit-transition: all 0.8s;
	-o-transition: all 0.8s;
	transition: all 0.8s;
}

a.icoGithub, a.icoLinkedin, a.icoEmail {
 background-color: #D3D3D3;
}



