/*
*  Nav Bar
*/



.fa-2x {
font-size: 2em;
}
.nav-fa {
position: relative;
display: table-cell;
width: 60px;
height: 40px;
text-align: center;
vertical-align: middle;
font-size:20px;
text-decoration: none;
}

.nav-a-Link {
  text-decoration: none;
  color: #999;
}

.main-menu:hover,nav.main-menu.expanded {
width:250px;
overflow:visible;
background-color: rgba(255,255,255,0.7);

}

.main-menu {
  padding-top: 40px;
background: rgba(255,255,255,0);
border-right-width: 2px;
border-right-style: solid;
border-right-image: linear-gradient(to bottom, #e5e5e5, rgba(0,0,0,0));
-webkit-border-image:
      -webkit-gradient(linear, 0 0, 0 100%, from(#e5e5e5), to(rgba(0, 0, 0, 0))) 1 100%;
    -webkit-border-image:
      -webkit-linear-gradient(#e5e5e5, rgba(0, 0, 0, 0)) 1 100%;
    -o-border-image:
      -o-linear-gradient(#e5e5e5, rgba(0, 0, 0, 0)) 1 100%;
position:absolute;
top:0;
bottom:0;
height:49vw;
left:1vw;
width: 70px;
overflow:hidden;
-webkit-transition:width .05s linear;
-o-transition:width .05s linear;
transition:width .05s linear;
-webkit-transform:translateZ(0) scale(1,1);
z-index:1000;
}

.main-menu>ul {
margin:7px 0;
}

.main-menu li {
position:relative;
display:block;
width:250px;
}

.main-menu li > .nav-a {
position:relative;
display:table;
border-collapse:collapse;
border-spacing:0;
color:#999;
 font-family: arial;
font-size: 14px;
text-decoration:none;
-webkit-transform:translateZ(0) scale(1,1);
-webkit-transition:all .1s linear;
-o-transition:all .1s linear;
transition:all .1s linear;

}

.main-menu .nav-icon {
text-decoration:none;
position:relative;
display:table-cell;
width:60px;
height:36px;
text-align:center;
vertical-align:middle;
font-size:18px;
}

.main-menu .nav-text {
text-decoration:none;
position:relative;
display:table-cell;
vertical-align:middle;
width:190px;
  font-family: 'Titillium Web', sans-serif;
}


.no-touch .scrollable.hover {
overflow-y:hidden;
}

.no-touch .scrollable.hover:hover {
overflow-y:auto;
overflow:visible;
}

.nav-a, .nav-a:hover,.nav-a:focus {
text-decoration:none;
}

.nav-a {
  color: #999;
  text-decoration:none;
}

.nav-a:hover {
  color: #fff;
}

.nav-a-Link:hover {
  color: #fff;
}

nav {
-webkit-user-select:none;
-moz-user-select:none;
-ms-user-select:none;
-o-user-select:none;
user-select:none;
}

nav ul, nav li {
outline:0;
margin:0;
padding:0;
}
.main-menu li:hover>.nav-a, nav.main-menu li.active>.nav-a, .dropdown-menu>li>.nav-a:hover, .dropdown-menu>li>.nav-a:focus, .dropdown-menu>.active>.nav-a, .dropdown-menu>.active>.nav-a:hover, .dropdown-menu>.active>.nav-a:focus, .no-touch .dashboard-page nav.dashboard-menu ul li:hover .nav-a, .dashboard-page nav.dashboard-menu ul li:active a {
color:#fff;
background-color:#5fa2db;
text-decoration:none;
}
.area {
float: left;
background: #e2e2e2;
width: 100%;
height: 100%;
}
@font-face {
  font-family: 'Titillium Web';
  font-style: normal;
  font-weight: 300;
}



